* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}
:root {
  --purple: linear-gradient(158deg, #d10ed1 0%, #10a3da 100%);
}
h3,
h2,
h4,
h5,
h6 {
  font-family: "Orbitron", sans-serif !important;
}
h2 {
  font-size: 45px;
}
h3 {
  font-size: 35px;
}
body {
  font-family: "Poppins", sans-serif;
}
p {
  margin-bottom: 0% !important;
  font-size: 20px;
  font-family: "Poppins", sans-serif;
  color: rgba(255, 255, 255, 0.8);
}
.f-14 {
  font-size: 14px !important;
  font-family: "Poppins", sans-serif;
}
.f-16 {
  font-size: 16px !important;
  font-family: "Poppins", sans-serif;
}
.f-18 {
  font-size: 18px !important;
  font-family: "Poppins", sans-serif;
  color: rgba(255, 255, 255, 0.8);
}
body {
  background: var(
    --1,
    linear-gradient(
      180deg,
      #010208 16.39%,
      #010206 20.51%,
      #040008 24.79%,
      #010101 30.46%,
      #010101 34.11%,
      #010101 36.21%,
      #010101 100%
    )
  );
}
a {
  text-decoration: none;
}
.gradient-text {
  background: var(--2, linear-gradient(158deg, #f86cf8 0%, #64cef5 100%));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.gradient-buttons {
  border-radius: 5px;
  background: linear-gradient(158deg, #d10ed1 0%, #10a3da 100%);
}
.gradient-border-buttons {
  border: 1px solid #d10ed1;
  border-radius: 5px;
  transition: 0.9s ease;
}
.gradient-border-buttons:hover {
  background: linear-gradient(158deg, #d10ed1 0%, #10a3da 100%);
  border: 1px solid #d10ed1;
}
